import React from 'react';
import PropTypes from 'prop-types';
import ReactHtmlParser from 'react-html-parser';


const Facultades = (props) => {

  const { list, reg, conj} = props;

  return (
    <div>
      <p>Regimen de administraci&oacute;n: La administraci&oacute;n de la sociedad y el uso de su raz&oacute;n social corresponden a {reg}{conj}.</p>
      <div>
        <ul className="lista">
          {ReactHtmlParser(list)}
        </ul>
      </div>
    </div>
  )
}

Facultades.propTypes = {
  list: PropTypes.string.isRequired,
  reg: PropTypes.string.isRequired,
  conj: PropTypes.string.isRequired
};

export default Facultades;