import React from 'react';
import PropTypes from 'prop-types';

const TableTest = (props) => {
  const { rows, headers } = props;
  const getHeaders = headers.map((i) => {
    return (
      <th>{i}</th>
    )
  })
  return (
    <table>
      <thead>
        <tr>
          {getHeaders}
        </tr>
      </thead>
      <tbody>
        {rows}
      </tbody>
    </table>
  )
}

TableTest.propTypes = {
  rows: PropTypes.element,
  headers: PropTypes.array
};

export default TableTest;