import React from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faTimes } from '@fortawesome/free-solid-svg-icons';

import PropTypes from 'prop-types';
import classNames from 'classnames';

import ReactHtmlParser from 'react-html-parser';

import TableTest from './../TableTest';
import Facultades from './../Facultades';

const Modal = (props) => {
  
  const { myData, isShow, fileName } = props;
  
  console.log('myData', myData);

  let comparecientes = '',
      conjuntamente = '',
      regimenAdm = '',
      poderes = '',
      compBool = false;

  // const startTable = '<table><thead><tr><th>Nombre</th><th>Rut</th><th>Domicilio</th></tr></thead><tbody>';
  // const endTable = '</tbody></table>';

  if (myData.hasOwnProperty("comparecientes")) {
    compBool = true;
    comparecientes = myData.comparecientes.map((index) => {
      return (
        <tr key={index.nombre}>
          <td>{index.nombre}</td>
          <td>{index.rut}</td>
          <td>{index.domicilio}</td>
        </tr>
      )
    })
  }

  if (myData.hasOwnProperty("comparecientes")) {
    compBool = true;
    if (myData.comparecientes.length > 1) {
      conjuntamente = ' todos conjunatemente';
    }
    myData.comparecientes.map((index) => {
      return regimenAdm += ' ' + index.nombre;
    })
  }

  if (myData.hasOwnProperty("poderes")) {
    for (var poder in myData.poderes) {
      poderes += '<li class="parrafos"><span>' + poder + ':</span> ' + myData.poderes[poder] + '</li>'
    }
  }

  const compHeader = ['Nombre', 'Rut', 'Domicilio'];
  return (
    <div className={classNames('modal large', { 'show': isShow })}>
      <div className="modal-container">
        <div className="modal-header">
          {fileName}
          <button>
            <FontAwesomeIcon icon={faTimes} />
          </button>
        </div>
        <div className="modal-body">
          <div className="block-33">
            <h3>Cabecera</h3>
            <div className="block-modal">
              <ul className="overflow">
                <li><span>Tipo de Sociedad:</span> {myData.tipo}</li>
                <li><span>Nombre de Sociedad:</span> {myData.nombre}</li>
                <li><span>Rut:</span> {myData.rut}</li>
                <li><span>Lugar de Escritura:</span> {myData.lugar}</li>
                <li><span>Fecha de Escritura:</span> {myData.fecha}</li>
                <li><span>Comparecientes:</span>
                  {compBool ? <TableTest key={compHeader} headers={compHeader} rows={comparecientes} /> : 'no hay comparecientes'}
                </li>
              </ul>
            </div>
          </div>

          <div className="block-33">
            <h3>Facultades</h3>
            <div className="block-modal">
              {poderes !== '' ? <Facultades list={poderes} reg={regimenAdm} conj={conjuntamente} /> : 'No se encontraron facultades'}
            </div>
          </div>

          {/* <p>Está seguro que desea cancelar la subida de archivos.</p>
          <p>Si presiona aceptar perderá el progreso de subida hasta el momento.</p> */}
        </div>
        <div className="modal-footer text-right">
          <a href="#;">Cancelar</a>
          {/* <button className="primary-button padding-x3 no-margin-bt margin-left-x2">Aceptar</button> */}
        </div>
      </div>
    </div>
  )

}

Modal.propTypes = {
  fileName: PropTypes.string.isRequired,
  myData: PropTypes.object.isRequired,
  isShow: PropTypes.bool.isRequired
};

