import React, { Component } from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faDownload } from '@fortawesome/free-solid-svg-icons';

import Header from '../../components/Header';
import Modal from './../../components/Modal';
// import CompanyList from './../../components/CompanyList';

import DocList from '../../components/DocList';
// import MenuApp from './../../components/MenuApp';

import classNames from 'classnames';
import Dropzone from 'react-dropzone';
// import { Redirect } from 'react-router'

import Notification from '../../components/Notification';
import axios from 'axios';

// import Check from './../Check';

// import Parche from '../../components/Parche';

class DocumentsAndContracts extends Component {

  constructor(props) {
    super(props);
    this.state = {
      acceptedFiles: [],
      message: '',
      active: false,
      classType: 'success',
      rejectedFiles: [],
      getFile: null,
      formValid: false,
      progress: 0,
      redirect: false,
      myData: {},
      isShow: false,
      fileName: ''
    }
  }

  timeOut = () => {
    setTimeout(() => {
      this.setState({
        active: false
      })
    },
      3500);
  }

  onDrop = (acceptedFiles, rejectedFiles) => {
    this.setState({
      acceptedFiles
    });
    this.setState({
      rejectedFiles
    });
    acceptedFiles.forEach(file => {

      console.log(file)

      this.setState({
        getFile: file,
        formValid: true,
        progress: 0,
        fileName: file.name
      });

    });

    rejectedFiles.forEach(file => {
      if (file.type !== "application/pdf") {
        this.setState({
          message: 'Solo se permiten archivos de tipo PDF',
          active: true,
          classType: 'error'
        })
      }
      this.timeOut();
    })

    if (rejectedFiles.length > 1) {
      console.log(rejectedFiles);
      this.setState({
        message: 'Solo se permite la subida de un archivo',
        active: true,
        classType: 'error'
      })
      this.timeOut();
    }

  }

  onCancel = () => {
    this.setState({
      acceptedFiles: []
    });
  }

  submitForm = (event) => {
    event.preventDefault();

    const config = {
      onUploadProgress: (progressEvent) => {
        console.log('progressEvent', progressEvent);
        
        var percentCompleted = Math.round((progressEvent.loaded * 100) / progressEvent.total);
        console.log(percentCompleted);
        console.log(progressEvent.loaded);
        this.setState({
          progress: percentCompleted
        })
      }
    }



    const theFile = new FormData();



    theFile.append("file", this.state.getFile);

    axios.post(`https://cariola-dev.mycognitiva.io/mad/carga_archivo`, theFile, config)
      .then(res => {
        if (res.data.estado.codigoEstado === 200) {

          console.log('esto hace algo');

          console.log(res.data);

          this.setState({
            myData: res.data.data,
            isShow: true
          });

          console.log('this.state.myData', this.state.myData)
          console.log('this.state.isShow', this.state.isShow)

          // componentDidMount() {
          //   console.log('GrandChild did mount.');
          // }

        } else {
          console.log('esto hace nada');
        }
      })
  }
    // ...
    // <Route path="/*" component={createRedirect('/foo')} />

  render() {

    // const { redirect } = this.state;

    // if (redirect) {

    //   console.log('deber?a redireccionar');
    //   // const getData = { 'llala': 'valor' };
    //   // const method = () => {
    //   //   return <Redirect from="/documents-contracts" push={true} to="/check" />
    //   // }

    //   // method().bind(this);
      
    //   // return <Parche myData={getData} />


    //   // const createRedirect = to => () => <Redirect to={to} />
      
    //   // const location = {
    //   //   pathname: '/somewhere',
    //   //   state: { fromDashboard: true }
    //   // }
    //   // history.push(location)


    //   // return <Route path="/*" component={createRedirect('/check')} />

        
        
    // }

    const acceptedFiles = this.state.acceptedFiles.map(acceptedFiles => {
      let getSize = acceptedFiles.size / 1024;
      return (
        <DocList
          key={acceptedFiles.name}
          name={acceptedFiles.name}
          size={getSize.toFixed(2)}
          progress={this.state.progress}
        />
      );
    })


    return (
      <div className="container heightfull bkg-views">
        <Notification
          message={this.state.message}
          active={this.state.active}
          classType={this.state.classType}
        />
        <div className="row heightfull no-flex">

          <Header />

          <div className="col-12 scroll">
            
            <div className="row">
              {/* <MenuApp /> */}
            </div>

            <div className="wrapper margin-tb-x1">
              <div className="row">
                
                <div className="col-12">

                  <h2>Cargar Archivos</h2>

                  {/* <p>Arrastra para cargar tu(s) archivo(s), el archivo o conjunto de los mismos, irán asociados al RUT y empresa seleccionada. Antes de cargarlos deberá seleccionar la empresa.</p> */}

                  <div className="block block-padding-x1">

                    <form onSubmit={this.submitForm}>
                      
                      {/* <fieldset>
                        <div className="section-number-lb">1.</div>
                        <CompanyList />
                      </fieldset> */}

                      <fieldset>
                        {/* <div className="section-number-lb">2.</div> */}
                        <Dropzone
                          accept=".pdf"
                          onDrop={this.onDrop}
                          multiple={false}>
                          {({getRootProps, getInputProps, isDragActive}) => {
                            return (
                              <div
                                {...getRootProps()}
                                className={classNames('file-upload dropzone', {'dropzone--isActive': isDragActive})}
                              >
                                <span className="file-icon">
                                  <FontAwesomeIcon icon={faDownload} />
                                </span>
                                <span className="file-title">Arrastra para cargar tu(s) archivo(s) o <span>busca en tu computador</span>.</span>
                                <input {...getInputProps()} />
                                {
                                  isDragActive ?
                                    <span className="file-paragraph">Suelta tus archivos aqu?</span> :
                                    <span className="file-paragraph">Se soportan archivos PDF y es permitido cualquier peso de archivo.</span>
                                }
                              </div>
                            )
                          }}
                        </Dropzone>

                      </fieldset>

                      <div className="doc-list">
                        {acceptedFiles}
                      </div>
                      
                      <div className="text-right">
                        {/* <a href="#;" className="no-margin-b">Cancelar</a> */}
                        <button className="primary-button margin-left-x2 no-margin-b padding-x3" disabled={!this.state.formValid}>Confirmar subida</button>
                      </div>
                      
                    </form>

                    {/* <Parche /> */}
                    
                  </div>

                </div>
                  
              </div>
            </div>
          </div>

        </div>

        <Modal
          key={this.state.fileName}
          fileName={this.state.fileName}
          myData={this.state.myData}
          isShow={this.state.isShow}
        />

      </div>

    );
  }
}

export default DocumentsAndContracts;