import React, { Component } from 'react';
import axios from 'axios';
import CryptoJS from 'crypto-js';

import logo from './../../assets/img/lexnova-logo.svg';
import cognitiva from './../../assets/img/cognitiva-logo.svg';
import cariola from './../../assets/img/cariola-logo.svg';
import isotipo from './../../assets/img/isotipo-lexnova.svg';

import Spinner from './../../components/Spinner';

class Login extends Component {
  
  constructor(props) {
    super(props);
    this.changeStatus = this.changeStatus.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
    this.state = {
      rut: '',
      clave: '',
      formErrors: { rut: '', clave: '' },
      errorMsg: '',
      rutValid: false,
      claveValid: false,
      formValid: false,
      token: ''
    }
  }

  handleUserInput = (e) => {

    const name = e.target.name;
    const value = e.target.value;
    const target = e.target;

    this.setState({
      [name]: value 
    }, () => {
        this.validateField(name, value, target)
      }
    );
  }

  changeMessage(el, msg) {
    el.parentElement.setAttribute('data-error', msg);
    return msg;
  }

  validateField(fieldName, value, target) {

    let fieldValidationErrors = this.state.formErrors,
        rutValid = this.state.rutValid,
        claveValid = this.state.claveValid;

    switch (fieldName) {
      case 'rut':
        rutValid = this.checkRut(value);
        fieldValidationErrors.rut = rutValid ? this.changeMessage(target, '') : this.changeMessage(target, 'El Rut es inválido');
        break;
      case 'clave':
        claveValid = value.length >= 6;
        fieldValidationErrors.clave = claveValid ? this.changeMessage(target, '') : this.changeMessage(target, 'La clave es incorrecta');
        break;
      default:
        break;
    }
    this.setState({
      formErrors: fieldValidationErrors,
      rutValid: rutValid,
      claveValid: claveValid
    }, this.validateForm);
  }

  validateForm() {
    this.setState({
      formValid: this.state.rutValid && this.state.claveValid
    });
  }

  changeStatus = (status, val) => {
    if (status === 200) {
      this.setState({
        token: val
      });
    }
  }

  getTkn = (event) => {
    event.preventDefault();
    axios.post(`https://cariola-dev.mycognitiva.io/mad/token`, {}, {
      headers: {
        Authorization: "Basic " + btoa("cognitiva:Cognitiv..")
      }
    }).then(res => {
      this.changeStatus(res.data.status, res.data.token);
      this.handleSubmit();
    })
  }

  handleSubmit = () => {

    const getDV = this.state.rut.substring(this.state.rut.length - 1),
          getRut = this.state.rut.substring(0, this.state.rut.length - 1);
    
    this.setState({
      rut: getRut + '-' + getDV
    });
    
    const userMd5 = '' + CryptoJS.MD5(this.state.rut),
          passMd5 = '' + CryptoJS.MD5(this.state.clave);

    const user = {
      user: userMd5,
      pass: passMd5
    };

    axios.post(`https://cariola-dev.mycognitiva.io/mad/login`, { user }, {
      headers: {
        Authorization: "Bearer " + this.state.token
      }
    }).then(res => {
      if (res.data.status === 200) {
        console.log('esto hace algo');
      } else {
        console.log('esto hace nada');
      }
      // console.log(res);
      // console.log(res.data);
    })
  }

  errorClass(error) {
    return (error.length === 0 ? '' : 'error');
  }

  checkRut(rut) {
    // Despejar Puntos
    let valor = rut.replace(/\./g, '');
    // Despejar Guión
    valor = valor.replace('-', '');
    // Aislar Cuerpo y Dígito Verificador
    let cuerpo = valor.slice(0, -1);
    let dv = valor.slice(-1).toUpperCase();
    // Formatear RUN
    rut = cuerpo + '-' + dv;
    // Si no cumple con el mínimo ej. (n.nnn.nnn)
    if (cuerpo.length < 7 && isNaN(cuerpo)) {
      return false;
    }
    // Calcular Dígito Verificador
    let suma = 0;
    let multiplo = 2;
        cuerpo = parseInt(cuerpo);
    // Para cada dígito del Cuerpo
    if (!isNaN(cuerpo)) {
      let length = Math.log(cuerpo) * Math.LOG10E + 1 | 0;
      for (let i = 1; i <= length; i++) {
        // Obtener su Producto con el Múltiplo Correspondiente
        let index = multiplo * valor.charAt(length - i);
        // Sumar al Contador General
        suma = suma + index;
        // Consolidar Múltiplo dentro del rango [2,7]
        if (multiplo < 7) {
          multiplo = multiplo + 1;
        } else {
          multiplo = 2;
        }
      }
    }
    // Calcular Dígito Verificador en base al Módulo 11
    let dvEsperado = 11 - (suma % 11);
    // Casos Especiales (0 y K)
    dv = (dv === 'K') ? 10 : dv;
    dv = (dv === 0) ? 11 : dv;
    // Validar que el Cuerpo coincide con su Dígito Verificador
    if (dvEsperado !== parseInt(dv)) {
      return false;
    }
    return true;
  }

  render() {
    return (
      <div className="container heightfull">
        <Spinner />
        <div className="row heightfull vert-align-item">

          <div className="col-5">
            <div className="row">
              <div className="col-8 col-offset-2">

                <div className="logo">
                  <img src={logo} alt="Lexnova" />
                </div>

                <h1>¡Bienvenido!</h1>
                <p>Por su seguridad y confidencialidad de la información, por favor ingrese su rut y clave alfanumérica</p>
                
                <form onSubmit={this.getTkn}>
                  
                  <fieldset
                    className={this.errorClass(this.state.formErrors.rut)}
                    data-error=""
                  >
                    <legend>Rut</legend>
                    <input
                      type="text"
                      name="rut"
                      placeholder="Ej. 11111111-1"
                      autoComplete="off"
                      value={this.state.rut}
                      onChange={this.handleUserInput}
                      />
                  </fieldset>

                  <fieldset
                    className={this.errorClass(this.state.formErrors.clave)}
                    data-error=""
                  >
                    <legend>Clave</legend>
                    <input
                      type="password"
                      name="clave"
                      placeholder="······"
                      autoComplete="off"
                      value={this.state.clave}
                      onChange={this.handleUserInput}
                    />
                  </fieldset>

                  <div className="text-right">
                    <a href="#;">Recuperar clave</a>
                  </div>

                  <button type="submit" className="primary-button block-button" disabled={!this.state.formValid}>Ingresar</button>

                  <div className="row logos">
                    <div className="col-5 col-offset-1">
                      <img src={cognitiva} alt="Cognitiva" />
                    </div>
                    <div className="col-5">
                      <img src={cariola} alt="Cariola" />
                    </div>
                  </div>

                </form>

              </div>
            </div>
          </div>

          <div className="col-7 heightfull bkg-login">
            <div className="row heightfull vert-align-item">
              <div className="col-8 col-offset-2">

                <div className="isotipo">
                  <img src={isotipo} alt="Lexnova" />
                </div>

                <div className="text-block">
                  <p>Desarrollamos soluciones de Inteligencia artificial</p>
                  <div className="palatino-text">En el Ámbito<br /> Legal</div>
                  <button className="padding-x3">Contacto</button>
                </div>

              </div>
            </div>
          </div>
        </div>
      </div>
    )
  }

}

export default Login;