import { APIURL } from "./constants"
import { BTOA } from "./constants";
import axios from 'axios';
import CryptoJS from 'crypto-js';

export function handleUserInput(e) {
  return function action(dispatch) {
    const name = e.target.name;
    const value = e.target.value;
    const target = e.target;

    this.setState({
      [name]: value
    }, () => {
      this.validateField(name, value, target)
    }
    );
  }
}

export function getTkn(event) {
  return function action(dispatch) {
    event.preventDefault();
    axios.post(APIURL + `token/`, {}, {
      headers: {
        Authorization: "Basic " + btoa(BTOA)
      }
    }).then(res => {
      this.changeStatus(res.data.status, res.data.token);
      this.handleSubmit();
    })
  }
}

export function handleSubmit() {
  return function action(dispatch) {
    const getDV = this.state.rut.substring(this.state.rut.length - 1),
          getRut = this.state.rut.substring(0, this.state.rut.length - 1);
    
    this.setState({
      rut: getRut + '-' + getDV
    });

    const userMd5 = '' + CryptoJS.MD5(this.state.rut),
          passMd5 = '' + CryptoJS.MD5(this.state.clave);

    const user = {
      user: userMd5,
      pass: passMd5
    };

    axios.post(APIURL +`login`, { user }, {
      headers: {
        Authorization: "Bearer " + this.state.token
      }
    }).then(res => {
      if (res.data.status === 200) {
        console.log('esto hace algo');
      } else {
        console.log('esto hace nada');
      }
      // console.log(res);
      // console.log(res.data);
    })
  }
}